body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  margin-top: 25px !important;
}

.card-header{
  background: #4a4a4a;
  border-radius: 1px;
}

.card-header-title{
  color: white !important;
}

#stocks_list .card-content{
  padding-left: 0px;
  padding-right: 0px;
}
#stocks_list table{
  width: 100%;
  margin: 3px;
}

.red{
  color: red;
}

.green{
  color: green;
}

.selected{
  background: #4dabf559;
  box-shadow: 1px 1px 5px #5555557d inset;
}

tbody tr{
  cursor: pointer;
}

td.updated_at{
  font-size: 12px;
}

.icon.market-trend{
  background: #e2e2e2;
  border-radius: 5px;
  margin-left: 3px;
}

.up-arrow{
  color: green;
}

.down-arrow{
  color: red;
}
